export const MenuIcon = (props) => {
	const lineExpanding = keyframes`
		from {
			left: 9px;
			width: 24px;
		}
		to {
			left: 0;
			width: 41px;
		}
	`;
	const MenuWrapper = styled('div')`
		position: relative;
		height: 24px;
		width: 41px;
		&:hover > *:not(:nth-child(2)) {
			animation: ${lineExpanding} 0.5s linear forwards;
		}
	`;
	const TopLine = styled('div')`
		position: absolute;
		width: 24px;
		height: 3px;
		border-radius: 5px;
		background-color: #0f0f0f;
		top: 0px;
		left: 9px;
	`;
	const MidLine = styled('div')`
		position: absolute;
		width: 41px;
		height: 3px;
		top: 9px;
		border-radius: 5px;
		background-color: #0f0f0f;
	`;
	const BotLine = styled('div')`
		position: absolute;
		width: 24px;
		height: 3px;
		top: 18px;
		left: 9px;
		border-radius: 5px;
		background-color: #0f0f0f;
	`;
	return (
	<MenuWrapper {...props}>
		<TopLine></TopLine>
		<MidLine></MidLine>
		<BotLine></BotLine>
	</MenuWrapper>
    );
};

