import { Box, AppBar, Toolbar, Typography, Grid} from "@mui/material";
// import { Cart, Hamburger, Search, User } from './menuItems'
import Cart from './menuItems/Cart'
import Hamburger from './menuItems/Hamburger'
import Search from './menuItems/Search'
import User from './menuItems/User'
import Hidden from '@mui/material/Hidden';
import { styled } from "@mui/system";
import ExpandableSearchBar from "./ExpandableSearchBar";

const Navbar = () => {
  return (
    <AppBar
      elevation={0}
      sx={{
        height: "75px",
        maxWidth: "1511px",
        backgroundColor: "#ffffff",
        color: "spiritBlack.main",
        position: 'static',
        margin: '0 auto',
      }}
    >
      <Toolbar disableGutters sx={{ width: '100%' }}>
        <Grid container wrap="nowrap" sx={{ width: '100%'}}>
          <Hidden mdUp>
            <Grid item xs={2} container alignContent="center">
              <Hamburger />
            </Grid>
            <Grid item xs={8}>
              <Typography variant="body1" fontSize="30px" textAlign="center">
                SPIRIT.
              </Typography>
            </Grid>
            <Grid item xs={2} container alignContent="center" justifyContent="flex-end">
              <Cart />
            </Grid>
          </Hidden>
          <Hidden mdDown>
            <Grid item xs={2}>
              <Typography variant="body1" fontSize="50px">
                SPIRIT.
              </Typography>
            </Grid>
            <Grid
              item
              container
              md={7}
              lg={8}
              gap={5}
              sx={{ alignContent: "center", justifyContent: "flex-end" }}
            >
              <Typography variant="body2">Home</Typography>
              <Typography variant="body2">Shop</Typography>
              <Typography variant="body2" onClick={() => console.log("blog")}>
                Blog
              </Typography>
              <Typography variant="body2">Contact</Typography>
            </Grid>
            <Grid
              item
              md={3}
              lg={2}
              container
              alignContent="center"
              justifyContent="flex-end"
              gap={3}
            > 
              <Search />
              <Cart />
              <User />
              <Hamburger />
            </Grid>
        </Hidden>
        </Grid>
      </Toolbar>
    </AppBar>
  );
};

export default Navbar;
