import { Typography, Hidden } from "@mui/material";
import { styled } from "@mui/system";
import SpiritButton from '../SpiritButton';


const Border = styled('div')`
    max-width: 1770px;
    max-height: 800px;
    height: 800px;
    border: solid 2px #fbfbfb;
    margin-top: 48px;
    margin-left: 25px;
    margin: 48px 25px 0 25px;
`

const Container = styled('div')`
    margin-top: 156px;
    margin-left: 127px;
    color: #fbfbfb;
    max-width: 900px;
`

const MobileContainer = styled('div')`
    margin-top: 111px;
    margin-left: auto;
    margin-right: auto;
    max-width: 247px;
    color: #fbfbfb;
`

function Banner() {
    return (
        <Border>
            <Hidden mdDown>
                <Container>
                    <Typography variant="body1">Elegant Wine from Georgia</Typography>
                    <Typography variant="h2" sx={{ mt: '5px' }}>The Wine Country.</Typography>
                    <Typography variant="body1" 
                    sx={{
                        mt: '46px',
                        display: "flex",
                        '&:before': {
                            display: "block",
                            content: '""',
                            borderTop: 'solid 2px #fbfbfb',
                            width: '103px',
                            marginTop: '17px',
                            marginRight: '25px'
                        }
                    }}>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam purus quam. Lorem ipsum dolor sit ame.</Typography>
                    <SpiritButton white sx={{
                        mt: '49px',
                        ml: '173px'
                    }}/>
                </Container>
            </Hidden>
            <Hidden mdUp>
                <MobileContainer>
                    <Typography variant="mbody" align="center" component="div">Elegant Wine from Georgia</Typography>
                    <Typography variant="h3" lineHeight="74px" align="center" letterSpacing="1.1px">The Wine Country.</Typography>
                    <Typography variant="mbody" pt="42px" align="center" lineHeight="30px" letterSpacing="0.35px" component="div">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam purus quam. Lorem ipsum dolor sit ame.</Typography>
                    <SpiritButton mobile white buttonText="Shop Now" sx={{ mt:"103px", ml:'10px'}} />
                </MobileContainer>
            </Hidden>
        </Border>
    );
  }
  
  export default Banner;
  
  
  