import SpiritWineItem from '../carousel/SpiritWineItem';
import { DownArrowIcon, RightArrowIcon } from '../../spiritIcons';
import { styled } from "@mui/system";
import { Box, Typography, Grid, Hidden} from "@mui/material";
import SpiritButtonCarousel from './SpiritButtonCarousel'
import LoadMore from '../LoadMore';

const Wrapper = styled('div')`
    display: flex;
    position: relative;
`

const WrapperRight = styled('div')`
    display: flex;
    position: relative;
    transform:rotate(180deg);
`

const ArrowLeftButton = styled('div')`
    width: 40px;
    height: 40px;
    border-top: solid 1px #202020;
    border-right: solid 1px #202020;
    border-bottom: solid 1px #202020;
    &:hover {
        cursor: pointer;
        background-color: #fff;
        left: -5px;
    }
`

const LeftGap = styled('div')`
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &:after {
        width: 1px;
        height: 15px;
        content: "";
        border-left: solid 1px #202020;
    }
    &:before {
        width: 1px;
        height: 15px;
        content: "";
        border-left: solid 1px #202020;
    }
`


const Carousel = (props) => {
    return (
        <>
            <Grid container mt="188px" justifyContent="center">
                <Hidden mdUp>
                    <Grid item container justifyContent="center">
                        <Typography variant="body1">We Recommend.</Typography>
                        <Grid item container justifyContent="center" gap={5} pt="28px">
                            <Grid item sx={{display: 'flex', alignItems: 'center'}}>
                                <Typography variant="body1" mr="6px">Wine</Typography>
                                <DownArrowIcon />
                            </Grid>
                            <Grid item sx={{display: 'flex', alignItems: 'center'}}>
                                <Typography variant="body1" mr="6px">Type</Typography>
                                <DownArrowIcon />
                            </Grid>
                            <Grid item sx={{display: 'flex', alignItems: 'center'}}>
                                <Typography variant="body1" mr="6px">Price</Typography>
                                <DownArrowIcon />
                            </Grid>
                            <Grid item sx={{display: 'flex', alignItems: 'center'}}>
                                <Typography variant="body1" mr="6px">Year</Typography>
                                <DownArrowIcon/>
                            </Grid>
                        </Grid>
                    </Grid>
                </Hidden>
                <Hidden mdDown>
                <Grid item container justifyContent="center">
                        <Typography variant="h3">We Recommend.</Typography>
                        <Grid item container justifyContent="center" gap={5} pt="28px">
                            <Grid item sx={{display: 'flex', alignItems: 'center'}}>
                                <Typography variant="body2" mr="6px">Wine</Typography>
                                <DownArrowIcon />
                            </Grid>
                            <Grid item sx={{display: 'flex', alignItems: 'center'}}>
                                <Typography variant="body2" mr="6px">Type</Typography>
                                <DownArrowIcon />
                            </Grid>
                            <Grid item sx={{display: 'flex', alignItems: 'center'}}>
                                <Typography variant="body2" mr="6px">Price</Typography>
                                <DownArrowIcon />
                            </Grid>
                            <Grid item sx={{display: 'flex', alignItems: 'center'}}>
                                <Typography variant="body2" mr="6px">Year</Typography>
                                <DownArrowIcon />
                            </Grid>
                        </Grid>
                    </Grid>
                </Hidden>
                <Hidden lgDown>
                    <Grid item container pt="63px">
                        <Grid item xs={1} container alignContent="center" justifyContent="center">
                            <Wrapper>
                                <LeftGap />
                                <ArrowLeftButton />
                                <RightArrowIcon sx={{ transform: "rotate(180deg)", position: "absolute", top: 8, right: 0, left: -8, bottom: 3,zIndex: 2}} />
                            </Wrapper>
                        </Grid>
                        <Grid item xs={10} sx={{display: 'flex', flexDirection: 'row', justifyContent: 'space-between'}}>
                            <SpiritWineItem />
                            <SpiritWineItem />
                            <SpiritWineItem />
                            <SpiritWineItem />
                            <SpiritWineItem />
                        </Grid>
                        <Grid item xs={1} container alignContent="center" justifyContent="center">
                            <WrapperRight>
                                <LeftGap />
                                <ArrowLeftButton />
                                <RightArrowIcon sx={{ transform: "rotate(180deg)", position: "absolute", top: 8, right: 0, left: -8, bottom: 3,zIndex: 2}} />
                            </WrapperRight>
                        </Grid>
                    </Grid>
                </Hidden>
                <Hidden lgUp>
                    <Grid item container mt="56px" justifyContent="center">
                        <Grid item container justifyContent="space-evenly" >
                            <SpiritWineItem />
                            <SpiritWineItem />
                        </Grid>
                        <Grid item container justifyContent="space-evenly" mt="60px">
                            <SpiritWineItem />
                            <SpiritWineItem />
                        </Grid>
                    </Grid>
                </Hidden>
                <Hidden mdDown>
                    <SpiritButtonCarousel sx={{ marginTop:"49px" }}/>
                </Hidden>
                <Hidden mdUp>
                    <Grid item container flexDirection="column" alignItems="center">
                        <LoadMore />
                        <SpiritButtonCarousel mobile sx={{ marginTop: '36px'}} />
                    </Grid>
                </Hidden>
            </Grid>
        </>
    )
}

export default Carousel;