import { Typography, Hidden } from "@mui/material";
import { styled } from "@mui/system";
import { ViewIcon, CartIcon, RightArrowIcon } from '../../spiritIcons';




const SpiritWineItem = () => {

    const BorderWrapper = styled('div')`
        position: relative;
    `

    const Border = styled('div')`
        height: 361px;
        width: 204px;
        border-left: solid 2px #202020;
        border-right: solid 2px #202020;
        &:hover {
            border-top: solid 2px #202020;
            border-bottom: solid 2px #202020;
        }
        &:hover > *:not(:nth-of-type(2)):before,
        &:hover > *:not(:nth-of-type(2)):after {
            width: 0px;
        }
        &:hover > :nth-of-type(1):after {
            z-index: -1;
            position: absolute;
            top: 178px;
            bottom: 0;
            left: -103px;
            right: 0;
            width: 411px;
            height: 1px;
            background-color: #202020;
            transform:rotate(-61deg);
        }
        &:hover > :nth-of-type(1):before {
            z-index: -1;
            position: absolute;
            top: 178px;
            bottom: 0;
            left: -103px;
            right: 0;
            width: 411px;
            height: 1px;
            background-color: #202020;
            transform:rotate(61deg);
        }
        &:hover > :nth-of-type(4) {
            display: flex;
        }
    `
    const TopBorder = styled('div')`
        display: flex;
        justify-content: space-between;
        &:before {
            display: block;
            content: '';
            width: 50px;
            border-top: 2px solid #202020;
        }
        &:after {
            display: block;
            content: '';
            width: 50px;
            border-top: 2px solid #202020;
        }
    `
    const BotBorder = styled('div')`
        display: flex;
        justify-content: space-between;
        &:before {
            display: block;
            content: '';
            width: 50px;
            border-bottom: 2px solid #202020;
        }
        &:after {
            display: block;
            content: '';
            width: 50px;
            border-bottom: 2px solid #202020;
        }
    `
    const Content = styled('div')`
        width: 100%;
        height: calc(100% - 4px);
        background-image: url(images/13.png);
        background-size:100% 100%;
        z-index: 1;
    `

    const IconButton = styled('button')`
        border: none;
        background-color: #262626;
        width: 60px;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: center;
        & > * {
            color: #fbfbfb;
        }
        &:hover {
            cursor: pointer;
            border: solid 1px #0f0f0f;
            background-color: #fbfbfb;
        }
        &:hover > :nth-of-type(1) {
            color: #202020
        }
    `
    const ButtonWrapper = styled('div')`
        display: none;
        gap: 12px;
        width: 132px;
        position: absolute;
        left: 34px;
        bottom: -12px;
        z-index: 2;
    `
    
    const WineWrapper = styled('div')`
        width: 200px;
    `
    

    return (
        <WineWrapper>
            <BorderWrapper>
                <Border>
                    <TopBorder />
                    <Content />
                    <BotBorder />
                    <ButtonWrapper>
                        <IconButton>
                            <CartIcon />
                        </IconButton>
                        <IconButton>
                            <ViewIcon />
                        </IconButton>
                    </ButtonWrapper>
                </Border>
            </BorderWrapper>
            <Typography variant="body2" mt="30px" align="center">White Dry Wine "Alaznis Veli"</Typography>
            <Typography variant="body1" mt="27px" align="center">30.00$</Typography>
        </WineWrapper>
    );
  }
  
  export default SpiritWineItem;
  
  
  