import {
    BrowserRouter as Router,
    Switch,
    Route
  } from "react-router-dom";
import MainPage from "./pages/mainPage"

function Application() {
  return (
    <Router>
        <Switch>
            <Route path="/test" render={() => <div>test</div>} />
            <Route path="/" render={() => <MainPage />} />
        </Switch>
    </Router>
  );
}

export default Application;


